.navbar {
  background-color: #d2e9a2;
  padding: 10px;
  color: #fff;
  
  display: flex;
  justify-content: space-between;
}

.view-btn-click{
  background-color: #d2e9a2;
  border-radius: 8px;
  border-width: 0;
  color: #333333;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
  padding: 10px 12px;
  text-align: center;
   margin-left: 5%;
}

.add-btn{
  border-radius: 5px;
  border: 0px solid black;
  padding: 5px 15px 5px 15px;
}

.search-btn{
border-radius: 5px;
width: 50%;
border: 1px solid black;
padding: 5px 15px 5px 15px;
}

.table-container {
  margin-top: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}

tr:hover {
  background-color: #dadada;
}


.details-popup {
  width: 50%;
  position: fixed;
  top: 50%;
 box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #d2e9a2;
  border-radius: 5px;
  padding: 30px;
  border-radius: 5px;
}
.view-btn{
position: relative;
top: 10px;
display: flex;
}

.view-save-btn{
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  border-width: 0;
  color: #333333;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
  padding: 10px 12px;
  text-align: center;
  
}
.view-save-btn:hover{

  background-color: rgba(75, 155, 44, 0.89);
  color: white;
  transition: 0.1s;
}
.view-cancel-btn{
    background-color: rgb(255, 255, 255);
    border-radius: 8px;
    border-width: 0;
    color: #333333;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    margin: 0;
    padding: 10px 12px;
    text-align: center;
     margin-left: 5%;
}
.view-cancel-btn:hover{
   background-color: rgb(195, 91, 91);
   color: white;
   transition: 0.1s;
}
.add-btn{
display: flex;
flex-direction: column;
column-gap: 40px;

}
.add-item{
  color: rgb(6, 0, 0);
  text-align: center;
}
.add-id{
  margin-top: 10px;

}
.add-name{
  margin-top: 10px;
}
.add-detail{
  margin-top: 10px;
}

.add-btns{
  margin-top: 20px;
  display: flex;
  padding: 5px 15px 5px 15px;

}
.add-btns .save-btn{
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  border-width: 0;
  color: #333333;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
  padding: 10px 12px;
  text-align: center;
}

.add-btns .save-btn:hover {

  background-color: rgba(75, 155, 44, 0.89);
  color: white;
  transition: 0.1s;
}

.add-btns .cancel-btn {
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  border-width: 0;
  color: #333333;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  margin: 0;
  padding: 10px 12px;
  text-align: center;
  margin-left: 5%;
}

.add-btns .cancel-btn:hover {
  background-color: rgb(195, 91, 91);
  color: white;
  transition: 0.1s;
}

.add-id,
.add-name,
.add-detail{
width: 100%;
padding: 15px 22px;
margin: 10px 5px;
border-radius: 10px;
border: 0px solid black;
box-sizing: border-box;
}
.popup {
  width: 40%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #d2e9a2;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 5px;
  padding: 10px;
  border-radius: 5px;
}

.popup-inner {
  background-color: #d2e9a2;
  border-radius: 5px;
}

.view-btn-error{
  display: flex;
  justify-content: space-between;
}



.close-btn {
 position: relative;
 top: 0px;
 right: 10px;
 text-align: end;

  border: none;
  background-color: red;
  cursor: pointer;
}


.container{
  display: flex;
  justify-content: space-between;
}

.table-data{
  border-radius: 10px;
   /* This removes the borders between cells */
  text-align: start;
   width: 40%;
   
}

.table-detail{
  border-radius: 10px;
   /* This removes the borders between cells */
  text-align: start;
   width: 40%;
   
}
.table-data th, td {
  padding: 8px;
  text-align: left;
}
.table-data th {
  background-color: #f2f2f200;
}
.Desc{
  display: flex;
}